import Api from '@/js/api/baseApi'

export default class adminApi extends Api {
  static adminList (data, callback, errorBack) {
    Api.request(this.uri() + 'admin_list', callback, errorBack, data)
  }

  static roleList (data, callback, errorBack) {
    Api.request(this.uri() + 'role', callback, errorBack, data)
  }

  static create (data, callback, errorBack) {
    Api.request(this.uri() + 'create', callback, errorBack, data)
  }

  static detail (data, callback, errorBack) {
    Api.request(this.uri() + 'detail', callback, errorBack, data)
  }

  static role (data, callback, errorBack) {
    Api.request(this.uri() + 'role', callback, errorBack, data)
  }

  static spinner (callback, errorBack) {
    Api.request(this.uri() + 'spinner', callback, errorBack)
  }

  static assignUserRole (data, callback, errorBack) {
    Api.request(this.uri() + 'assign_user_role', callback, errorBack, data)
  }

  static pwd (data, callback, errorBack) {
    Api.request(this.uri() + 'change_password', callback, errorBack, data)
  }

  static info (callback, errorBack) {
    Api.request(this.uri() + 'info', callback, errorBack)
  }

  static uri () {
    return Api.url() + 'admin/'
  }
}
