import Api from '@/js/api/baseApi'

export default class {
  static index (data, callback, errorBack) {
    Api.request(this.uri() + 'index', callback, errorBack, data)
  }

  static select (data, callback, errorBack) {
    Api.request(this.uri() + 'select', callback, errorBack, data)
  }

  static save (data, callback, errorBack) {
    Api.request(this.uri() + 'save', callback, errorBack, data)
  }

  static detail (data, callback, errorBack) {
    Api.request(this.uri() + 'detail', callback, errorBack, data)
  }

  static delete (data, callback, errorBack) {
    Api.request(this.uri() + 'delete', callback, errorBack, data)
  }

  static member (data, callback, errorBack) {
    Api.request(this.uri() + 'member', callback, errorBack, data)
  }

  static selectDepartment (data, callback, errorBack) {
    Api.request(this.uri() + 'select_department', callback, errorBack, data)
  }

  static selectMember (data, callback, errorBack) {
    Api.request(this.uri() + 'select_member', callback, errorBack, data)
  }

  static leader (data, callback, errorBack) {
    Api.request(this.uri() + 'leader', callback, errorBack, data)
  }

  static uri () {
    return Api.url() + 'department/'
  }
}
