<template>

  <div>
    <h3>管理员信息编辑</h3>
    <Divider orientation="left">信息填写</Divider>
    <Row>
      <Col :xs="24" :sm="24" :md="12" :lg="12">
        <Form :model="formData" :label-width="80">
          <FormItem v-if="!this.$route.query.id" label="手机">
            <Input v-model="formData.mobile" placeholder="请填写手机"></Input>
          </FormItem>

          <FormItem v-if="!this.$route.query.id" label="密码">
            <Input v-model="formData.password" placeholder="请填写密码" type="password"></Input>
          </FormItem>

          <FormItem v-if="!this.$route.query.id" label="确认密码">
            <Input v-model="formData.confirm_pass" type="password" placeholder="请填写确认密码"></Input>
          </FormItem>

          <FormItem label="状态">
            <Select v-model="formData.status">
              <Option label="禁用" :value="0"></Option>
              <Option label="正常" :value="1"></Option>
            </Select>
          </FormItem>

          <FormItem label="真实姓名">
            <Input v-model="formData.real_name" placeholder="请填写真实姓名"></Input>
          </FormItem>

          <FormItem label="运营团队">
            <Select @on-change="loadRoleAndDepartment" v-model="formData.operation_team_id">
              <Option v-for="(item,index) in teams" v-bind:key="index" :label="item.name"
                      :value="item.id"></Option>
            </Select>
          </FormItem>

          <FormItem label="角色">
            <Select v-model="formData.role_id" multiple>
              <Option v-for="(item,index) in roles" v-bind:key="index" :label="item.name"
                      :value="item.id"></Option>
            </Select>
          </FormItem>

          <FormItem v-if="formData.operation_team_id===0" label="部门">
            <Cascader :data="departments" v-model="department" change-on-select></Cascader>
          </FormItem>

          <FormItem>
            <Button type="success" long @click="submit">提交</Button>
          </FormItem>
        </Form>
        <Spin size="large" fix v-if="load"></Spin>
      </Col>

    </Row>
  </div>
</template>

<script>
import teamApi from '../../operating/api'
import departmentApi from '../department/api'
import api from './api'

export default {
  name: 'edit',
  data () {
    return {
      formData: {},
      teams: [],
      roles: [],
      departments: [],
      department: [],
      defaultDepartment: null,
      defaultDepartments: [],
      load: false
    }
  },
  mounted () {
    this.loadTeam()
    if (this.$route.query.id != null) {
      this.loadData()
    }
    if (this.$route.query.operation_team_id != null) {
      this.formData.operation_team_id = parseInt(this.$route.query.operation_team_id)
    }
  },
  methods: {
    loadTeam () {
      teamApi.select((res) => {
        console.assert(res)
        this.teams = res.data.data
      })
    },
    loadRoleAndDepartment (id) {
      api.role({ operation_team_id: id }, (res) => {
        this.roles = res.data.data
      })
      this.formData.role_id = []
      this.formData.department_id = []
      if (id !== 0) {
        return
      }
      departmentApi.select({ operation_team_id: id }, (res) => {
        this.initDepartment(this.departments, res.data.data)
        if (this.$route.query.id != null && this.defaultDepartment == null) {
          this.defaultDepartments = res.data.data
          this.initDefaultDepartment(this.formData.department.id, this.defaultDepartments)
          this.department = this.department.reverse()
        }
      })
    },
    initDefaultDepartment (id, list) {
      let _this = this
      list.forEach(value => {
        if (value.id === id) {
          _this.department.push(value.id)
          if (value.p_id !== 0) {
            _this.initDefaultDepartment(value.p_id, this.defaultDepartments)
          }
          return
        }
        if (value.child != null) {
          _this.initDefaultDepartment(id, value.child)
        }
      })
    },

    initDepartment (value, list) {
      let _this = this
      list.forEach(value1 => {
        let item = {
          value: value1.id,
          label: value1.name,
          children: []
        }
        if (value1.child != null) {
          _this.initDepartment(item.children, value1.child)
        }
        value.push(item)
      })
    },
    loadData () {
      this.load = true
      api.detail(
        { id: this.$route.query.id },
        res => {
          this.load = false
          if (res.data.data.operation_team) {
            res.data.data.operation_team_id = res.data.data.operation_team.id
          }
          if (res.data.data.department) {
            res.data.data.department_id = res.data.data.department.id
          }
          if (res.data.data.role) {
            res.data.data.role_id = []
            res.data.data.role.forEach(value => {
              res.data.data.role_id.push(value.id)
            })
          }
          this.loadRoleAndDepartment(res.data.data.operation_team.id)
          this.formData = res.data.data
        }, () => {
          this.load = false
        })
    },
    submit () {
      if (this.$route.query.id === null) {
        if (!this.$formUtil.isStrValid(this.formData.mobile)) {
          this.$Message.error('请输入手机号')
          return
        }
        if (!this.$formUtil.isStrValid(this.formData.password)) {
          this.$Message.error('请输入密码')
          return
        }
        if (!this.$formUtil.isStrValid(this.formData.confirm_pass)) {
          this.$Message.error('请输入确认密码')
          return
        }
      }
      if (!this.$formUtil.isStrValid(this.formData.real_name)) {
        this.$Message.error('请输入真实姓名')
        return
      }
      if (!this.$formUtil.isStrValid(this.formData.operation_team_id)) {
        this.$Message.error('请选择运营团队')
        return
      }

      if (this.department == null || this.department.length === 0) {
        this.$Message.error('请选择部门')
        return
      } else {
        this.formData.department_id = this.department[this.department.length - 1]
      }
      if (!this.$formUtil.isStrValid(this.formData.role_id)) {
        this.$Message.error('请选择角色')
        return
      }

      this.load = true
      api.assignUserRole(this.formData, res => {
        this.load = false
        this.$router.back()
      }, () => {
        this.load = false
      })
    }
  }
}
</script>

